import { Grid } from '@mui/material';
import React from 'react';
import { FormatUtil } from '../../../../util/format.util';
import { Alert } from '../alert';

export interface EventListLoadingProps {
  error: any
  loading: boolean
  count: number
  message: string
}

export const EventListLoading: React.FC<EventListLoadingProps> = (props: EventListLoadingProps) => {
  return (
    <Grid item key='1' xs={12} md={6}>
      {props.loading ? (
        <Alert message="Loading..." type="default" ></Alert>
      ) : (
        <>
          {props.error ? (
            <Alert message={FormatUtil.formatMessage(props.error)} type="warning"></Alert>
          ) : (
            <>
              {props.count > 0 ? (
                <></>
              ) : (
                <Alert message={props.message} type="default"></Alert>
              )}
            </>
          )}
        </>
      )}
    </Grid>
  )
}
