import { useQuery } from '@apollo/client';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Avatar, Button, Grid, Link, Paper, styled, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import { BigNumber } from 'bignumber.js';
import React, { useState } from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import { Link as RouterLink } from 'react-router-dom';
import { ActionName, CommonConstants } from '../../../../common/constants';
import { useConnectedWeb3Context } from '../../../../contexts/connectedWeb3';
import { GET_PUNK_QUERY } from '../../../../queries';
import { GetPunk, GetPunkVariables } from '../../../../queries/__generated__/GetPunk';
import { getMarketService } from '../../../../services/market.service';
import { IconUtil } from '../../../../util/icon.util';
import { getLogger } from '../../../../util/logger';
import { MomentUtil } from '../../../../util/moment.util';
import { SnackbarUtil } from '../../../../util/snackbar.util';
import { TokenUtil } from '../../../../util/token.util';
import { AccountAuction, AccountAuctionType, Punk, PunkDialogState, PunkDialogType } from '../../../../util/types';
import { UrlUtil } from '../../../../util/url.util';
import { ClickOnceButton } from '../../../common/components/click-once-button';
import { EventListLoading } from '../../../common/components/event-list-loading';
import { ExtendedBootstrapTooltip } from '../../../common/components/tooltip';
import { PunkDialog } from '../../../punk/components/dialog';

const logger = getLogger();

interface EventTableRowProps {
  isAuthorized: boolean
  item: AccountAuction
  handleClaimAsync(_item: AccountAuction): Promise<void>
  handleOpenDialog(_type: PunkDialogType, _item: Punk, _auction: AccountAuction): void
}

const CustomAvatar = styled(Avatar)(({ theme }) => (`
  background-image: url('${CommonConstants.ITEM_BG_URL}');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  image-rendering: pixelated;
  margin-right: 10px;
`));

const CustomTableCell = styled(TableCell)(({ theme }) => (`
  background-color: rgba(255, 255, 255, 0.3);
`));

const EventTableRow: React.FC<EventTableRowProps> = (props: EventTableRowProps) => {
  const [isOwner, setIsOwner] = useState(false);

  const context = useConnectedWeb3Context();

  const query = useQuery<GetPunk, GetPunkVariables>(GET_PUNK_QUERY, {
    variables: {
      id: props.item.punkId.toString()
    },
    fetchPolicy: 'network-only',
    onCompleted: (data) => {
      setIsOwner(data.punk.owner?.toLowerCase() === context.account?.toLowerCase());
    }
  });
  const momentUtil = new MomentUtil();

  const formatTimestamp = (timestamp: any) => {
    return momentUtil.getLocalReverseFormatted(momentUtil.getFromUnix(timestamp));
  }

  const formatViteTooltip = (bn: BigNumber) => {
    return TokenUtil.formatViteTooltip(bn);
  }

  const formatVite = (bn: BigNumber) => {
    return TokenUtil.formatVite(bn);
  }

  const getEventName = (): string => {
    switch (props.item.auctionType) {
      case AccountAuctionType.Offer:
        if (!props.item.amount || props.item.amount.lte(0)) {
          return ActionName.gift;
        }
        return ActionName.list;
      case AccountAuctionType.Bid:
        return ActionName.bid;
      default:
        break;
    }
    return ActionName.unknown;
  }

  const handleClaimAsync = async () => {
    if (query.data?.punk) {
      await props.handleClaimAsync(props.item);
    }
  }

  const handleOpenDialog = (_type: PunkDialogType) => {
    if (query.data?.punk) {
      props.handleOpenDialog(_type, query.data.punk, props.item);
    }
  }

  return (
    <TableRow sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
      <TableCell component="th" scope="row">
        <FontAwesomeIcon icon={IconUtil.getAccountAuctionTypeIcon(props.item.auctionType, props.item.amount)} /> <b>{getEventName()}</b>
      </TableCell>
      <TableCell>
        <Link underline="hover" component={RouterLink} to={`/punk/${props.item.punkId}`} display="flex" alignItems="center">
          <CustomAvatar variant="rounded" src={UrlUtil.getPunkUrl(query.data?.punk.image, query.data?.punk.cid)}>
          </CustomAvatar>
          {CommonConstants.PUNK_NAME} #{props.item.punkId}
        </Link>
      </TableCell>
      <TableCell>
        {props.item.amount?.gt(0) ? (
          <>
            <ExtendedBootstrapTooltip title={formatViteTooltip(props.item.amount)}>
              <span>{formatVite(props.item.amount)}</span>
            </ExtendedBootstrapTooltip>
          </>
        ) : (
          <>
          </>
        )}
      </TableCell>
      <TableCell>
        {formatTimestamp(props.item.timestamp.toFixed())}
      </TableCell>
      <TableCell align="right">
        {props.item.auctionType === AccountAuctionType.Offer && (
          <>
            {!isOwner ? (
              <>
                {!props.item.amount || props.item.amount.lte(0) ? (
                  <ClickOnceButton size="small" variant="outlined" color="secondary" sx={{ minWidth: "70px" }} callbackFn={handleClaimAsync} disabled={!props.isAuthorized}>{ActionName.claim}</ClickOnceButton>
                ) : (
                  <Button size="small" variant="outlined" sx={{ minWidth: "70px" }} onClick={() => { handleOpenDialog(PunkDialogType.ACCEPT_OFFER) }} disabled={!props.isAuthorized}>{ActionName.buy}</Button>
                )}
              </>
            ) : (
              <Button size="small" variant="outlined" sx={{ minWidth: "70px" }} onClick={() => { handleOpenDialog(PunkDialogType.CANCEL_OFFER) }} disabled={!props.isAuthorized}>{ActionName.cancel}</Button>
            )}
          </>
        )}
        {props.item.auctionType === AccountAuctionType.Bid && (
          <>
            {!isOwner ? (
              <Button size="small" variant="outlined" sx={{ minWidth: "70px" }} onClick={() => { handleOpenDialog(PunkDialogType.CANCEL_BID) }} disabled={!props.isAuthorized}>{ActionName.cancel}</Button>
            ) : (
              <Button size="small" variant="outlined" sx={{ minWidth: "70px" }} color="secondary" onClick={() => { handleOpenDialog(PunkDialogType.ACCEPT_BID) }} disabled={!props.isAuthorized}>{ActionName.accept}</Button>
            )}
          </>
        )}
      </TableCell>
    </TableRow>
  )
}

interface Props {
  events: AccountAuction[]
  newEvents: AccountAuction[]
  error: any
  loading: boolean
  hasMore: boolean
  height?: number
  fetchMoreDataAsync(): Promise<void>
  cleanupAsync(_item: AccountAuction): Promise<void>
  isAuthorized: boolean
}

export const AccountAuctionList: React.FC<Props> = (props: Props) => {
  const [currentAuction, setCurrentAuction] = useState<AccountAuction>({} as AccountAuction);
  const [dialogState, setDialogState] = useState<PunkDialogState>({
    type: PunkDialogType.UNKNOWN,
    open: false,
    item: {} as Punk
  });

  const service = getMarketService();
  const emptyMessage = "No active auctions";

  const handleClaimAsync = async (_item: AccountAuction) => {
    try {
      if (_item.auctionType !== AccountAuctionType.Offer) {
        throw new Error(`${CommonConstants.PUNK_NAME} #${_item.punkId} cannot be claimed`);
      }
      const success = await service.buyAsync(_item.punkId.toString(), _item.amount.toFixed());
      if (success) {
        SnackbarUtil.enqueueMessage(`Congratulations! You claimed ${CommonConstants.PUNK_NAME} #${_item.punkId}`);
      }
    } catch (error) {
      SnackbarUtil.enqueueError(error);
    } finally {
      props.cleanupAsync(_item);
    }
  }

  const handleOpenDialog = (_type: PunkDialogType, _item: Punk, _auction: AccountAuction) => {
    setCurrentAuction(_auction);
    setDialogState({
      ...dialogState,
      type: _type,
      open: true,
      item: _item
    });
  }

  const dialogCallbackAsync = async (_success: boolean) => {
    logger.info("dialogCallbackAsync", _success)();
    if (_success) {
      props.cleanupAsync(currentAuction);
    }
  }

  return (
    <>
      <Paper
        id="event-list"
        style={{
          height: props.height ?? 300,
          overflow: 'auto',
          backgroundColor: 'rgba(255, 255, 255, 0.5)'
        }}
      >
        {props.newEvents.length > 0 || props.events.length > 0 ? (
          <InfiniteScroll
            dataLength={props.events.length ?? 0}
            next={props.fetchMoreDataAsync}
            hasMore={props.hasMore}
            loader={
              <Grid container spacing={3} justifyContent="center" alignItems="center">
                <EventListLoading loading={props.loading} error={props.error} count={props.events.length} message={emptyMessage}></EventListLoading>
              </Grid>
            }
            scrollableTarget="event-list"
          >
            <TableContainer>
              <Table stickyHeader>
                <TableHead>
                  <TableRow>
                    <CustomTableCell></CustomTableCell>
                    <CustomTableCell><b>Punk</b></CustomTableCell>
                    <CustomTableCell><b>Amount</b></CustomTableCell>
                    <CustomTableCell><b>Date</b></CustomTableCell>
                    <CustomTableCell align="right"><b>Action</b></CustomTableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {props.newEvents.map((e: AccountAuction) => (
                    <EventTableRow
                      key={`new-events-${e.timestamp.toFixed()}`}
                      isAuthorized={props.isAuthorized}
                      handleClaimAsync={handleClaimAsync}
                      handleOpenDialog={handleOpenDialog}
                      item={e} />
                  ))}
                  {props.events.map((e: AccountAuction) => (
                    <EventTableRow
                      key={`events-${e.timestamp.toFixed()}`}
                      isAuthorized={props.isAuthorized}
                      handleClaimAsync={handleClaimAsync}
                      handleOpenDialog={handleOpenDialog}
                      item={e} />
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </InfiniteScroll>) : (
          <Grid sx={{ height: "100%" }} container spacing={3} justifyContent="center" alignItems="center">
            <EventListLoading loading={props.loading} error={props.error} count={props.events.length} message={emptyMessage}></EventListLoading>
          </Grid>
        )}
      </Paper>
      <PunkDialog state={dialogState} setState={setDialogState} callbackAsync={dialogCallbackAsync}></PunkDialog>
    </>
  )
}
