import { ApolloError } from '@apollo/client';
import { Box, Container, Grid } from '@mui/material';
import React from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import { FormatUtil } from '../../../../util/format.util';
import { Punk } from '../../../../util/types';
import { Alert } from '../../../common/components/alert';
import { ClickOnceButton } from '../../../common/components/click-once-button';
import { MarketplaceBrowseItem } from '../../../marketplace/components/browse-item';

interface EmptyResultProps {
  error: ApolloError | undefined
  isLoading: boolean
  isEmpty: boolean
}

const EmptyResult: React.FC<EmptyResultProps> = (props: EmptyResultProps) => {
  return (
    <>
      {props.error ? (
        <Alert message={FormatUtil.formatMessage(props.error)} type="warning"></Alert>
      ) : (
        <>
          {props.isLoading || !props.isEmpty ? (
            <></>
          ) : (
            <Alert message="No punks found" type="default" ></Alert>
          )}
        </>
      )}
    </>
  )
}

interface InfiniteListLoadingProps {
  error: ApolloError | undefined
  isLoading: boolean
  isEmpty: boolean
}

const InfiniteListLoading: React.FC<InfiniteListLoadingProps> = (props: InfiniteListLoadingProps) => {
  return (
    <Grid container spacing={3} justifyContent="center" alignItems="center">
      <Grid item key='1' xs={12} md={6}>
        {props.isLoading ? (
          <Alert message="Loading..." type="default" ></Alert>
        ) : (
          <EmptyResult error={props.error} isLoading={props.isLoading} isEmpty={props.isEmpty}></EmptyResult>
        )}
      </Grid>
    </Grid>
  )
}

interface InfinitePunkListProps {
  items: Punk[]
  fetchMoreDataAsync(): Promise<any>
  error: ApolloError | undefined
  isLoading: boolean
  hasMore: boolean
}

export const InfinitePunkList: React.FC<InfinitePunkListProps> = (props: InfinitePunkListProps) => {
  return (
    <>
      <Container sx={{ pt: 3, pb: 6 }} maxWidth="xl">
        {props.items.length > 0 ? (
          <>
            <InfiniteScroll
              dataLength={props.items.length}
              next={props.fetchMoreDataAsync}
              hasMore={props.hasMore}
              loader={
                <InfiniteListLoading error={props.error} isLoading={props.isLoading} isEmpty={props.items.length <= 0}></InfiniteListLoading>
              }
              scrollThreshold="200px"
              scrollableTarget="mainscroll"
            >
              <Grid container spacing={3} justifyContent="center" alignItems="center" marginBottom={3}>
                {props.items.map((p: Punk) => (
                  <Grid item key={p.id} xs={12} sm={6} md={4} lg={3}>
                    <MarketplaceBrowseItem item={p}></MarketplaceBrowseItem>
                  </Grid>
                ))}
              </Grid>
            </InfiniteScroll>
            {props.hasMore && !props.isLoading && (
              <Box sx={{ width: "100%", display: "flex", justifyContent: "center" }}>
                <ClickOnceButton variant="outlined" callbackFn={props.fetchMoreDataAsync} sx={{ minWidth: "200px" }}>Load more</ClickOnceButton>
              </Box>
            )}
          </>
        ) : (
          <InfiniteListLoading error={props.error} isLoading={props.isLoading} isEmpty={props.items.length <= 0}></InfiniteListLoading>
        )}
      </Container>
    </>
  )
}
