import { useQuery } from '@apollo/client';
import SyncIcon from '@mui/icons-material/Sync';
import { IconButton, Stack } from '@mui/material';
import React, { useCallback, useEffect, useState } from 'react';
import { useConnectedWeb3Context } from '../../../../contexts/connectedWeb3';
import { GET_ACCOUNT_AUCTIONS_QUERY } from '../../../../queries/general';
import { GetAccountAuctions, GetAccountAuctionsVariables } from '../../../../queries/__generated__/GetAccountAuctions';
import { getGeneralService } from '../../../../services/general.service';
import { CommonUtil } from '../../../../util/common.util';
import { getEmitter } from '../../../../util/emitter';
import { getLogger } from '../../../../util/logger';
import { TransformUtil } from '../../../../util/transform.util';
import { AccountAuction, Cursor, DocumentState, GlobalEvent, PunkChangedEvent } from '../../../../util/types';
import { AccountAuctionList } from '../auction-list';

const logger = getLogger();

interface Props {
  address: string
  refetchDataAsync(): Promise<void>
}

export const AccountAuctions: React.FC<Props> = (props: Props) => {
  const limit = 8;
  const [isAuthorized, setIsAuthorized] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const [error, setError] = useState<any>(undefined);
  const [cursor, setCursor] = useState<Maybe<Cursor>>(undefined);
  const [events, setEvents] = useState<AccountAuction[]>([]);
  const [newEvents, setNewEvents] = useState<AccountAuction[]>([]);
  const service = getGeneralService();
  const emitter = getEmitter();

  const context = useConnectedWeb3Context();

  const query = useQuery<GetAccountAuctions, GetAccountAuctionsVariables>(GET_ACCOUNT_AUCTIONS_QUERY, {
    variables: {
      address: props.address,
      limit: limit
    },
    //notifyOnNetworkStatusChange: true,
    fetchPolicy: 'network-only',
    //refetchWritePolicy: 'overwrite',
    onCompleted: (data) => {
      setCursor(data.accountAuctions.cursor);
      setEvents(data.accountAuctions.auctions.map(e => TransformUtil.toAccountAuction(e)));
      setHasMore(!CommonUtil.isNullOrWhitespace(data.accountAuctions.cursor?.next));
    }
  });

  useEffect(() => {
    setIsAuthorized(props.address?.toLowerCase() === context.account?.toLowerCase());
  }, [props.address, context.account]);

  useEffect(() => {
    setIsLoading(query.loading);
  }, [query.loading]);

  useEffect(() => {
    setError(query.error);
  }, [query.error]);

  const fetchMoreDataAsync = async () => {
    logger.info("fetchMoreDataAsync", events.length)();
    setIsLoading(true);
    try {
      const result = await service.getAccountAuctions(props.address, limit, cursor?.next);
      setCursor(result.data.accountAuctions.cursor);
      setHasMore(!CommonUtil.isNullOrWhitespace(result.data.accountAuctions.cursor?.next));
      if (result.data.accountAuctions.auctions.length > 0) {
        setEvents([
          ...events,
          ...result.data.accountAuctions.auctions.map(e => TransformUtil.toAccountAuction(e))
        ]);
      }
    } catch (error) {
      setError(error);
    }
    setIsLoading(false);
  };

  const cleanupAsync = async (_item: AccountAuction) => {
    if (_item) {
      setEvents(events.filter(e => !e.timestamp.eq(_item.timestamp)));
      setNewEvents(newEvents.filter(e => !e.timestamp.eq(_item.timestamp)));
    }
    // Update all auctions before refetch
    await service.getAccountAuctions(props.address, limit);
    await props.refetchDataAsync();
  }

  const refetch = query.refetch;

  const refresh = useCallback(() => {
    logger.info("Refreshing account auctions")();
    refetch();
  }, [refetch]);

  const refreshAsync = async () => {
    logger.info("Refreshing account auctions & data")();
    await refetch();
    await props.refetchDataAsync();
  };

  useEffect(() => {
    const handleEvent = (state: DocumentState) => {
      if (state === DocumentState.Active) {
        refresh();
      }
    }
    emitter.on(GlobalEvent.DocumentVisibilityChanged, handleEvent);
    return () => {
      emitter.off(GlobalEvent.DocumentVisibilityChanged, handleEvent);
    };
  }, [emitter, refresh]);

  const addNewEvent = useCallback((e: PunkChangedEvent) => {
    // TODO: check if new event is related to current address
    setNewEvents([
      // TransformUtil.toPunkEvent(e),
      ...newEvents
    ]);
  }, [newEvents]);

  useEffect(() => {
    const handleEvent = (e: PunkChangedEvent) => {
      logger.info("newMarketEvent", e)();
      addNewEvent(e);
    }
    logger.info('emitter.on', GlobalEvent.PunkChanged)();
    emitter.on(GlobalEvent.PunkChanged, handleEvent);
    return () => {
      logger.info('emitter.off', GlobalEvent.PunkChanged)();
      emitter.off(GlobalEvent.PunkChanged, handleEvent);
    };
  }, [emitter, addNewEvent]);

  return (
    <>
      <Stack direction="row" justifyContent="flex-end" alignItems="center" spacing={2}>
        <IconButton size="small" aria-label="refresh" component="span" onClick={refreshAsync}>
          <SyncIcon fontSize="small" />
        </IconButton>
      </Stack>
      <AccountAuctionList
        events={events}
        newEvents={newEvents}
        error={error}
        loading={isLoading}
        hasMore={hasMore}
        height={300}
        fetchMoreDataAsync={fetchMoreDataAsync}
        cleanupAsync={cleanupAsync}
        isAuthorized={isAuthorized}>
      </AccountAuctionList>
    </>
  )
}
