import { Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { ActionName, CommonConstants } from '../../../../common/constants';
import { getMarketService } from '../../../../services/market.service';
import { SnackbarUtil } from '../../../../util/snackbar.util';
import { PunkDialogState, PunkDialogType } from '../../../../util/types';
import { ClickOnceButton } from '../../../common/components/click-once-button';

interface Props {
  state: PunkDialogState
  setState: React.Dispatch<React.SetStateAction<PunkDialogState>>
  callbackAsync: (_success: boolean) => Promise<void>
}

export const PunkCancelDialog: React.FC<Props> = (props: Props) => {
  const [actionName, setActionName] = useState("");
  const service = getMarketService();

  useEffect(() => {
    setActionName(props.state.type === PunkDialogType.CANCEL_BID ? ActionName.bid : (props.state.item.offer?.minAmount.gt(0) ? ActionName.listing : ActionName.gift));
  }, [props.state]);

  const handleClose = () => {
    props.setState({
      ...props.state,
      open: false
    })
  };

  const handleConfirmAsync = async () => {
    let _success = false;
    try {
      if (props.state.type === PunkDialogType.CANCEL_BID) {
        _success = await service.cancelBidAsync(props.state.item.id);
      } else {
        _success = await service.cancelOfferAsync(props.state.item.id);
      }
      if (_success) {
        SnackbarUtil.enqueueMessage(`${actionName} cancelled`);
        handleClose();
      } else {
        SnackbarUtil.enqueueError("Error");
      }
    } catch (error) {
      _success = false;
      SnackbarUtil.enqueueError(error);
    } finally {
      await props.callbackAsync(_success);
    }
  }

  return (
    <Dialog onClose={handleClose} open={props.state.open} maxWidth="xs" fullWidth>
      <DialogTitle>Cancel</DialogTitle>
      <DialogContent>
        <DialogContentText>
          Cancel the {actionName.toLowerCase()} for {CommonConstants.PUNK_NAME} #{props.state.item.id}
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose}>Close</Button>
        <ClickOnceButton size="medium" color="primary" callbackFn={handleConfirmAsync}>
          Confirm
        </ClickOnceButton>
      </DialogActions>
    </Dialog>
  )
}
