import { CommonConfig } from "../common/config";
import { CommonConstants } from "../common/constants";
import { CommonUtil } from "./common.util";

export abstract class UrlUtil {
  static getPunkUrl(image?: Maybe<string>, cid?: Maybe<string>): string {
    if (image && !CommonUtil.isNullOrWhitespace(image)) {
      if (CommonConfig.USE_IPFS) {
        if (cid && !CommonUtil.isNullOrWhitespace(cid)) {
          return UrlUtil.getPunkIpfsUrlByCid(cid);
        }
        return UrlUtil.getPunkIpfsUrl(image);
      } else {
        return `https://cdn.vitepunks.com/punks/iso/${image}`;
      }
    }
    return "";
  }
  static getPunkIpfsUrlByCid(cid?: Maybe<string>): string {
    if (cid && !CommonUtil.isNullOrWhitespace(cid)) {
      return `${CommonConstants.IPFS_GATEWAY_URL}${cid}`;
    }
    return "";
  }
  static getPunkIpfsUrl(image?: Maybe<string>): string {
    if (image && !CommonUtil.isNullOrWhitespace(image)) {
      return `${CommonConstants.IPFS_GATEWAY_URL}QmRGJyWMZidhH35J5tfn7vWwR1MfhPdSZh7FEXWXub4zJm/${image}`;
    }
    return "";
  }
}