
import { Chip, Typography } from '@mui/material';
import { useEffect, useState } from 'react';
import { Footer } from '../../../layout/components/footer';
import { HeaderLoading } from '../../../layout/components/header-loading';
import { MainScroll } from '../../../layout/components/main-scroll';
import { MainWrapper } from '../../../layout/components/main-wrapper';

interface Props {
  retryCount: number
}

export const MainLoading: React.FC<Props> = (props: Props) => {
  const [retryCount, setRetryCount] = useState<number>(0)

  useEffect(() => {
    setRetryCount(props.retryCount)
  }, [props.retryCount])

  return (
    <MainWrapper>
      <HeaderLoading />
      <MainScroll>
        <Typography>Loading...</Typography>
        {retryCount > 0 && (
          <Chip size="small" label={retryCount} variant="outlined" />
        )}
      </MainScroll>
      <Footer />
    </MainWrapper>
  )
}
