import { useQuery } from '@apollo/client';
import React, { useEffect, useState } from 'react';
import { DefaultPunkFilterValues } from '../../../../common/constants';
import { GET_PUNKS_QUERY } from '../../../../queries';
import { GetPunks, GetPunksVariables } from '../../../../queries/__generated__/GetPunks';
import { getEmitter } from '../../../../util/emitter';
import { getLogger } from '../../../../util/logger';
import { GlobalEvent, PunkFilterValues } from '../../../../util/types';
import { OrderBy, PunkSortBy } from '../../../../__generated__/globalTypes';
import { InfinitePunkList } from '../../../punk/components/infinite-list';
import { MarketplaceBrowseItemsFilter } from '../browse-items-filter';

const logger = getLogger()

export const MarketplaceBrowseItems: React.FC = (props: any) => {
  const limit = 8;
  const [isLoading, setIsLoading] = useState(false);
  const [hasMore, setHasMore] = useState(false);
  const [error, setError] = useState<any>(undefined);
  const [filter, setFilter] = useState<PunkFilterValues>({
    sortBy: DefaultPunkFilterValues.sortBy,
    orderBy: DefaultPunkFilterValues.orderBy,
    search: ''
  })
  const emitter = getEmitter();

  const query = useQuery<GetPunks, GetPunksVariables>(GET_PUNKS_QUERY, {
    variables: {
      offset: 0,
      limit: limit,
      sort: filter.sortBy as PunkSortBy,
      order: filter.orderBy as OrderBy,
      search: filter.search
    },
    fetchPolicy: 'network-only',
    onCompleted: (data) => {
      setHasMore(data.punks.length >= limit);
    }
  });

  useEffect(() => {
    setIsLoading(query.loading);
  }, [query.loading]);

  useEffect(() => {
    setError(query.error);
  }, [query.error]);

  const fetchMoreDataAsync = async () => {
    const startTimestamp = new Date().getTime();
    const currentLength = query.data?.punks.length ?? 0;
    setIsLoading(true);
    try {
      const result = await query.fetchMore({
        variables: {
          offset: currentLength,
          limit: limit
        }
      });
      logger.info(`fetchMoreDataAsync1 took ${new Date().getTime() - startTimestamp}ms`)();
      setHasMore(result.data.punks.length >= limit);
    } catch (error) {
      setError(error);
    }
    setIsLoading(false);
    logger.info(`fetchMoreDataAsync2 took ${new Date().getTime() - startTimestamp}ms`)();
  };

  const refetch = query.refetch;

  useEffect(() => {
    refetch({
      offset: 0,
      limit: limit,
      sort: filter.sortBy as PunkSortBy,
      order: filter.orderBy as OrderBy,
      search: filter.search
    })
  }, [filter, refetch])

  useEffect(() => {
    const handleEvent = (oldValues: PunkFilterValues, newValues: PunkFilterValues) => {
      setHasMore(true);
      setFilter({
        sortBy: newValues.sortBy,
        orderBy: newValues.orderBy,
        search: newValues.search
      });
    }
    emitter.on(GlobalEvent.PunkFilterValuesChanged, handleEvent)
    return () => {
      logger.info('emitter.off', GlobalEvent.PunkFilterValuesChanged)()
      emitter.off(GlobalEvent.PunkFilterValuesChanged, handleEvent)
    };
  }, [emitter])

  return (
    <>
      <MarketplaceBrowseItemsFilter></MarketplaceBrowseItemsFilter>
      <InfinitePunkList
        items={query.data?.punks ?? []}
        fetchMoreDataAsync={fetchMoreDataAsync}
        error={error}
        isLoading={isLoading}
        hasMore={hasMore}>
      </InfinitePunkList>
    </>
  )
}
