import { Button, Dialog, DialogActions, DialogContent, DialogTitle, FormControl, Input, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { CommonConstants } from '../../../../common/constants';
import { getMarketService } from '../../../../services/market.service';
import { SnackbarUtil } from '../../../../util/snackbar.util';
import { TokenUtil } from '../../../../util/token.util';
import { PunkDialogState, PunkDialogType } from '../../../../util/types';
import { ClickOnceButton } from '../../../common/components/click-once-button';

interface Props {
  state: PunkDialogState
  setState: React.Dispatch<React.SetStateAction<PunkDialogState>>
  callbackAsync: (_success: boolean) => Promise<void>
}

export const PunkAcceptDialog: React.FC<Props> = (props: Props) => {
  const [amount, setAmount] = useState("");
  const service = getMarketService();

  useEffect(() => {
    if (props.state.type === PunkDialogType.ACCEPT_OFFER) {
      if (props.state.item.offer?.minAmount.gt(0)) {
        setAmount(TokenUtil.formatViteTooltip(props.state.item.offer.minAmount))
      }
    } else {
      if (props.state.item.bid?.amount.gt(0)) {
        setAmount(TokenUtil.formatViteTooltip(props.state.item.bid.amount))
      }
    }
  }, [props.state]);

  const handleClose = () => {
    props.setState({
      ...props.state,
      open: false
    })
  };

  const handleConfirmAsync = async () => {
    let _message = `Congratulations! You %ACTION_NAME ${CommonConstants.PUNK_NAME} #${props.state.item.id}`;
    let _success = false;
    try {
      if (props.state.type === PunkDialogType.ACCEPT_OFFER) {
        if (!props.state.item.offer) {
          throw new Error(`${CommonConstants.PUNK_NAME} #${props.state.item.id} is not for sale`);
        }
        _success = await service.buyAsync(props.state.item.id, props.state.item.offer.minAmount.toFixed());
        _message = _message.replace("%ACTION_NAME", "bought");
      } else {
        if (!props.state.item.bid) {
          throw new Error(`${CommonConstants.PUNK_NAME} #${props.state.item.id} has no bid`);
        }
        _success = await service.acceptBidAsync(props.state.item.id, props.state.item.bid?.amount.toFixed());
        _message = _message.replace("%ACTION_NAME", "sold");
      }
      if (_success) {
        SnackbarUtil.enqueueMessage(_message);
        handleClose();
      } else {
        SnackbarUtil.enqueueError("Error");
      }
    } catch (error) {
      _success = false;
      SnackbarUtil.enqueueError(error);
    } finally {
      await props.callbackAsync(_success);
    }
  }

  return (
    <Dialog onClose={handleClose} open={props.state.open} maxWidth="xs" fullWidth>
      <DialogTitle>Confirmation required</DialogTitle>
      <DialogContent>
        {props.state.type === PunkDialogType.ACCEPT_OFFER ? (
          <Typography variant="body1" color="textSecondary" gutterBottom>
            Buy {CommonConstants.PUNK_NAME} #{props.state.item.id}
          </Typography>
        ) : (
          <>
            <Typography variant="body1" color="textSecondary" gutterBottom>
              Accept bid for {CommonConstants.PUNK_NAME} #{props.state.item.id}
            </Typography>
            {/* <Typography variant="body1" color="textSecondary">
              You will receive <small>(minus fees)</small>:
            </Typography> */}
          </>
        )}
        <FormControl fullWidth sx={{ mt: 2 }} variant="standard" >
          <Input
            id="amount"
            value={amount}
            //startAdornment={<InputAdornment position="start">${CommonConstants.CURRENCY_NAME}</InputAdornment>}
            disabled
          />
        </FormControl>
      </DialogContent>
      <DialogActions>
        <Button onClick={handleClose}>Close</Button>
        <ClickOnceButton size="medium" color="primary" callbackFn={handleConfirmAsync}>
          Confirm
        </ClickOnceButton>
      </DialogActions>
    </Dialog>
  )
}
