import { useQuery } from '@apollo/client';
import { Container, Grid, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { GET_PUNKS_BY_ADDRESS_QUERY } from '../../../../queries';
import { GetPunksByAddress, GetPunksByAddressVariables } from '../../../../queries/__generated__/GetPunksByAddress';
import { InfinitePunkList } from '../../../punk/components/infinite-list';
import { AccountAuctions } from '../auctions';

export const AccountDetails: React.FC = (props: any) => {
  const { address } = useParams();
  const limit = 8;
  const [isLoading, setIsLoading] = useState(false);
  const [hasMore, setHasMore] = useState(false);
  const [error, setError] = useState<any>(undefined);

  const query = useQuery<GetPunksByAddress, GetPunksByAddressVariables>(GET_PUNKS_BY_ADDRESS_QUERY, {
    variables: {
      address: address ?? "",
      offset: 0,
      limit: limit
    },
    fetchPolicy: 'network-only',
    onCompleted: (data) => {
      setHasMore(data.punksByAddress.length >= limit);
    }
  });

  useEffect(() => {
    setIsLoading(query.loading);
  }, [query.loading]);

  useEffect(() => {
    setError(query.error);
  }, [query.error]);

  const fetchMoreDataAsync = async () => {
    const currentLength = query.data?.punksByAddress.length ?? 0;
    setIsLoading(true);
    try {
      const result = await query.fetchMore({
        variables: {
          offset: currentLength,
          limit: limit
        }
      });
      setHasMore(result.data.punksByAddress.length >= limit);
    } catch (error) {
      setError(error);
    }
    setIsLoading(false);
  };

  const refetchDataAsync = async () => {
    await query.refetch({
      address: address ?? "",
      offset: 0,
      limit: limit
    });
  }

  return (
    <>
      <Container maxWidth="lg" sx={{ marginBottom: 5 }}>
        <Grid container spacing={3} justifyContent="center" alignItems="center">
          <Grid item xs={12} md={9}>
            <Typography variant="h2" component="div" textAlign="center" sx={{ pt: 3 }}>
              Account
            </Typography>
            <Typography variant="subtitle1" component="div" textAlign="center" sx={{ wordBreak: "break-all" }} gutterBottom>
              {address}
            </Typography>
          </Grid>
          <Grid item xs={12} md={9}>
            <AccountAuctions address={address ?? ""} refetchDataAsync={refetchDataAsync}></AccountAuctions>
          </Grid>
        </Grid>
      </Container>
      <InfinitePunkList
        items={query.data?.punksByAddress ?? []}
        fetchMoreDataAsync={fetchMoreDataAsync}
        error={error}
        isLoading={isLoading}
        hasMore={hasMore}>
      </InfinitePunkList>
    </>
  )
}
