import { CommonConfig } from "../common/config";
import { CommonConstants, Networks } from "../common/constants";
import { getMockDataSource, IDataSource } from "../datasources";
import { getViteDataSource } from "../datasources/vite.datasource";
import { getLogger } from "../util/logger";
import { Network } from "../util/types";
import { UrlUtil } from "../util/url.util";

const logger = getLogger();

export class CommonContext {
  private _isInitialized: boolean = false;
  private _network!: Network;
  private _datasource!: IDataSource;

  async initAsync(): Promise<void> {
    this._isInitialized = false;
    if (CommonConstants.USE_MOCK_DATASOURCE) {
      this._datasource = getMockDataSource();
    } else {
      this._datasource = getViteDataSource();
    }
    const networkId = CommonConfig.NETWORK_ID;
    this._network = Networks.find(e => e.id === networkId) ?? Networks[0];
    await this._datasource.initBaseAsync(this._network);
    await this.checkIpfsAsync();
    logger.info("Using IPFS", CommonConfig.USE_IPFS)();
    this._isInitialized = true;
  }

  private async checkIpfsAsync(): Promise<void> {
    return new Promise(async (resolve) => {
      const punk = await this._datasource.getPunkAsync("1");
      var img = new Image();
      img.onload = () => {
        CommonConfig.USE_IPFS = false;
        resolve();
      };
      img.onerror = () => {
        CommonConfig.USE_IPFS = true;
        resolve();
      };
      img.src = UrlUtil.getPunkUrl(punk.image);
    });
  }

  dispose(): void {
    logger.info("Disposing CommonContext")();
    this._datasource?.dispose();
  }

  get isInitialized(): boolean {
    return this._isInitialized;
  }

  get network(): Network {
    return this._network;
  }

  get datasource(): IDataSource {
    return this._datasource;
  }
}

const context = new CommonContext();

export const getCommonContext = () => {
  return context;
}
