import { gql } from "@apollo/client";
import { ApolloContext } from "../clients/apollo.client";
import { AccountAuctions, ViteQuotaResult } from "../util/types";

export const GET_QUOTA_QUERY = gql`
  query GetQuota($address: String!) {
    quota(address: $address) @client {
      currentQuota
      maxQuota
      stakeAmount
    }
  }
`;

export const GET_ACCOUNT_AUCTIONS_QUERY = gql`
  query GetAccountAuctions($address: String!, $limit: Int!, $cursor: String) {
    accountAuctions(address: $address, limit: $limit, cursor: $cursor) @client {
      cursor {
        prev
        next
      }
      auctions {
        timestamp
        auctionType
        punkId
        owner
        amount
        address
      }
    }
  }
`;

export const GeneralQueries = {
  async quota(parent: any, { address }: any, context: ApolloContext): Promise<Maybe<ViteQuotaResult>> {
    const result = await context.client.datasource.getQuotaAsync(address);
    return result;
  },
  async accountAuctions(parent: any, { address, limit, cursor }: any, context: ApolloContext): Promise<AccountAuctions> {
    const result = await context.client.datasource.getAccountAuctionsAsync(address, limit, cursor);
    return result;
  },
}